import { Component, OnInit, Input, Output } from '@angular/core';
import { Crc16Component } from '../crc16/crc16.component';
import { ActivatedRoute } from '@angular/router';
import { HttpClient} from '@angular/common/http';
import { DecimalPipe } from '@angular/common';

import { LedcommandsComponent } from '../ledcommands/ledcommands.component';

@Component({
  selector: 'app-wsupdate-prices',
  templateUrl: './update-wsprices.component.html',
  styleUrls: ['./update-wsprices.component.css']
})

export class WsUpdatePricesComponent implements OnInit {

   magna:string = "";
   premium:string = "";
   diesel:string = "";
   jsonData:any = null;

  constructor(private http: HttpClient, private decPipe:DecimalPipe, private router:ActivatedRoute) { }

  ngOnInit() {

    var ledComponent = new LedcommandsComponent(this.http, this.decPipe, this.router) ;
    console.log("update prices component...");
    
    try{
       this.readUpdatedPrices();
       //this.magna = pricesObj.magna;
       //this.premium = pricesObj.premium;
       //this.diesel = pricesObj.diesel;
    }
    catch(err){
      this.magna = "";
      this.premium = ""; 
      this.diesel = "";
      console.log("UpdatePrices Error: " + err);
    }
  }

  readUpdatedPrices(){
    console.log("reading updated prices....");
    this.http.get('http://104.154.33.238:8080/archivo/a').subscribe(data => {
      // Read the result field from the JSON response.
       var obj:any = data;
       var prices = JSON.parse(obj.prices);
  
       this.magna = this.validate(prices.magna);
       this.premium = this.validate(prices.premium);
       this.diesel = this.validate(prices.diesel);
    });
  }

  validate(price):string{
    var result:string = "";

    if(price == undefined){
      result = "";
    }
    else if(price <= 0){
      result = "";
    }
    else{
      result = this.decPipe.transform(price, "2.2-2");  //reference-->> https://angular.io/api/common/DecimalPipe    2.2-2 -->> Max Integers 2; MinDecimal Places 2; MaxDecimal Places 2
    }

    return result;
  }

 


}



